  border-radius: 8px;
  &_img {
    position: relative;
    font-size: 0;
    min-height: 80px;
    img {
      width: 100%;
    }
  }

  li {
    height: 24px;
    line-height: 24px;
  }
}
.price_icon {
  width: 8px;
  height: 14px;
}
.color-btn {
  width: 10px;
  height: 10px;
}
.pixel_x {
  margin-right: 5px;
}
.pixel-btn {
  margin-top: 10px;
}
.price {
  margin-left: 4px;
}
.item_color {
  display: inline-block;
  width: 120px;
  height: 25px;
  line-height: 25px;
  background: rgba($white, 0.3);
  border: 1px solid $white;
  opacity: 1;
  border-radius: 19px;
  bottom: 10px;
  overflow: hidden;
  // -webkit-filter: blur(1px);
  span {
    z-index: 99;
  }
  &::after {
    content: '';
    width: 120px;
    height: 25px;
    background: url('../assets/marketImg/pixel-icon.png');
    background-position: center bottom;
    background-size: cover;
    background-attachment: fixed;
    position: absolute;
    filter: blur(4px);
    top: 0;
    left: 0;
  }
}
.pixel_xy {
  transform: scale(0.85);
}

