export const mathFloor = (number, digit) => {
  if (isNaN(Number(number)) || isNaN(Number(digit))) {
    return 0;
  }
  return (
    Math.floor(number * Math.pow(10, digit) + 1 / Math.pow(10, 9 - digit)) /
    Math.pow(10, Number(digit))
  ).toFixed(Number(digit));
};
